import React,{Suspense,lazy} from 'react'
import Loading from "../../../components/shared-components/Loading";
import {Redirect, Route, Switch} from "react-router-dom";

const TravelViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover={'page'}/>}>
            <Switch>
                <Route path={`${ match.url }/list`} component={lazy(()=> import("./travel-list/index"))}/>
                <Route path={`${ match.url }/add`} component={lazy(()=> import("./travel-add/index"))}/>
                <Route path={`${ match.url }/category`} component={lazy(()=> import("./travel-category/TravelCategoryViews"))}/>
                <Route path={`${ match.url }/p/:id`} component={lazy(()=> import("./travel-single/index"))}/>
                <Route path={`${ match.url }/update/:id`} component={lazy(()=> import("./travel-update/index"))}/>
                <Redirect from={match.url} to={`${match.url}/list`}/>
            </Switch>
        </Suspense>
    )
}

export default TravelViews
